import { useContext, useEffect, useMemo, useRef } from "react";
import styled from "styled-components";
import { DetailChord } from "../../../../../redux/store/scoreData";
import { TIMELINE_BEAT_WIDTH } from "../../../../../utils/systemConst";
import { GlobalContext } from "../../../../entry/systemEntry";
import BlockItem from "./blockItem";

const HarmTimeline = () => {

    const { store, dispatcher } = useContext(GlobalContext);
    /** Ref */
    const scrollRef = useRef({} as HTMLDivElement);

    const state = store.scoreState;
    const dataList = store.scoreData.chordList;

    const blockItemJsxList: JSX.Element[] = useMemo(() => {
        const list: JSX.Element[] = [];

        dataList.forEach((data, i) => {
            switch (data.type) {
                case 'chord': {
                    list.push(
                        <BlockItem key={list.length} index={i}/>
                    );
                }
                    break;
            }
        });
        return list;
    }, [dataList.length]);

    useEffect(()=> {
        // scrollRef.current.scrollTo({ left: total * MEM_WIDTH - FIXED_BLANK, behavior: 'smooth' });
    }, [state.focusIndex]);

    if (state.focusIndex === -1 || state.focusIndex > state.elementCacheList.length) {
        return <></>;
    }

    return (
        <_Wrap ref={scrollRef}>
            <_Blocks>{blockItemJsxList}</_Blocks>
            <_Measure>

            </_Measure>
        </_Wrap>
    );
}

export default HarmTimeline;


const _Wrap = styled.div`
    overflow-x: hidden;
    text-align: left;
    white-space: nowrap;  
`;

const _Blocks = styled.div`
    display: block;
    /* position: relative; */
    height: 46px;
    /* width: 2200px; */
    background-color: rgb(221, 221, 221);
`;

const _Measure = styled.div`
    display: inline-block;
    position: relative;
    height: 50px;
    /* background-color: rgb(253, 250, 236); */
    border-top: solid 2px rgb(128, 128, 128);
    background: linear-gradient(to bottom, rgb(255, 255, 255), rgba(0, 0, 0, 0.349));
`;