import { useContext } from "react";
import styled from "styled-components";
import { ExecutableButton } from "../../../base/components/button/executableButton";
import { ChordBacking, DetailChord, DetailFixed, DetailInit, DetailSection, ScoreData } from "../../../redux/store/scoreData";
import { GlobalContext } from "../../entry/systemEntry";

const ManageTab = () => {

    const { store, dispatcher } = useContext(GlobalContext);

    return (
        <_Wrap>
            <ExecutableButton labelText={<>Blank</>} size={'midium'} width={100} callback={() => {

                const data: ScoreData = {
                    chordList: [
                        { type: 'init', detail: { keyIndex: 0, bpm: 100, scale: 'major', timeSignature: '4/4' } as DetailInit },
                        { type: 'fixed', detail: { fixed: 'Start' } as DetailFixed },
                        { type: 'section', detail: { labelName: 'Section' } as DetailSection },
                        {
                            type: 'chord', detail: {
                                beatLen: 4, minute: { head: 0, tail: 0 }, root: null, on: null, symbolKey: 'major',
                                backing: {
                                    voicingList: [],
                                    pattern: null
                                }
                            } as DetailChord
                        },
                        { type: 'fixed', detail: { fixed: 'End' } as DetailFixed },
                    ],
                    notesList: []
                };
                dispatcher.score.setScoreData(data);
                store.scoreState.focusIndex = 2;
                dispatcher.score.setScoreState({ ...store.scoreState });

                dispatcher.score.setScoreTabIndex(1);
                // dispatcher.score.setScoreTabIndex(2);
            }} />
        </_Wrap>
    );
}

export default ManageTab;

const _Wrap = styled.div`
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: #d6e5f1;
`;